import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import AdminMenu from './AdminMenu';
import { propShowCount } from '../validation/BackendUrl';

export default function Offered() {
    const [skipCnt, setSkipCnt] = useState(0);
    const [servErr, setSrvErr] = useState(false);
    const [list, setList] = useState([]);
    const [propListLoad, setPropListLoad] = useState(false);
    const [showMoreBtn, setShowMoreBtn] = useState(false);
    useEffect(() => {
        listCall('load');
    }, []);
    const loadMore = ()=> {
        listCall('push');
    }
    function listCall(mrge) {
        setPropListLoad(true);
        let token = localStorage.getItem("admin");
        if (token) {
            axios.get('api/adminoffered/'+skipCnt, {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setSkipCnt(skipCnt+propShowCount);
                if(mrge === 'push') {
                    setList(oldArray => [...oldArray, ...res.data]);
                } else {
                    setList(res.data);
                }
                if(res.data.length === propShowCount) {
                    setShowMoreBtn(true);
                } else {
                    setShowMoreBtn(false);
                }
                setPropListLoad(false);
            })
            .catch((err) => {
                setSrvErr(true);
                setPropListLoad(false);
            });
        }
    }
    const checkPr = (e, id, ind) => {
        e.target.disabled = true;
        let token = localStorage.getItem("admin");
        if (token) {
            axios.get('api/admofferdone/'+id, {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                let newArr = [...list];
                newArr[ind].read = 'yes';
                setList(newArr);
            })
            .catch((err) => {
                console.log(err);
            });
        }
    }
    return (
        <div className="admin-pages container">
            <div className="row">
                <div className="col-md-3">
                    <AdminMenu />
                </div>
                {!servErr?<div className='col-md-9'>
                    <h3 className="mb-4">Offered Properties with offer price</h3>
                    {list.length > 0?<table className="table table-bordered">
                        <thead>
                            <tr>
                                <th scope="col">Sl No.</th>
                                <th scope="col">By</th>
                                <th scope="col">Mobile</th>
                                <th scope="col">Property</th>
                                <th scope="col">Offer Price</th>
                                <th scope="col">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                            {list.map((lst, index) =>
                                <tr key={index} className={lst.read === 'yes'?'checked':''}>
                                    <td>{index+1}</td>
                                    <td>{lst.name}</td>
                                    <td>{lst.mobile}</td>
                                    <td><Link to={'/property/'+lst.prop_id} target="_blank">View Detail</Link></td>
                                    <td>{lst.offerVal}/-</td>
                                    <td>{lst.read === 'yes'?<span>Checked</span>:<button disabled={false} onClick={(e)=>checkPr(e, lst._id, index)} className='adm_btn'>Done</button>}</td>
                                </tr>
                            )}
                        </tbody>
                    </table>:<div className="error-page text-center">
                        <div style={{height:"60px"}}>&nbsp;</div>
                        <h5 className="not_fnd_hd">NO ITEMS FOUND</h5>
                        <p className="not_fnd_body">
                            Please try again later or<br />
                            <Link to="/admindashboard" className="readmore-btn">Back to dashboard</Link>
                        </p>
                    </div>}
                    {propListLoad?<div className="ad_list_loader">Loading...</div>:showMoreBtn?<button className='site-btn sb-big load-more' onClick={loadMore}>Load More</button>:""}
                </div>:<div className="col-9">
                    <div className="error-page text-center">
                        <div style={{height:"60px"}}>&nbsp;</div>
                        <h5 className="not_fnd_hd">OOPS... Something went wrong!</h5>
                        <p className="not_fnd_body">
                            Please try again later or<br />
                            <Link to="/" className="readmore-btn">Back to Home Page</Link>
                        </p>
                    </div>
                </div>}
            </div>
        </div>
    );
}
